@font-face {
  font-family: "wonder";
  src: url("/assets/fonts/subset-Wonder-Bold.woff2") format("woff2"), url("/assets/fonts/subset-Wonder-Bold.woff") format("woff");
  font-weight: 800;
  font-style: bold;
}
:root {
  --font-stack-heading: "wonder", Georgia, "Times New Roman", Times, "SongTi TC", serif;
  --color-1: #1c1d1d;
  --color-2: #fff;
  --color-3: #fec545;
}

main {
  max-width: 45em;
}

main p, main dl {
  max-width: 35em;
  margin-left: auto;
  margin-right: auto;
}

header {
  margin-top: 0;
}

h1 {
  text-transform: none;
  line-height: 1.1;
  margin-top: 1em;
}

h2 {
  font-weight: bold;
  font-style: normal;
  color: var(--color-3);
}

